<script>
import RoundedRect from "../primitives/RoundedRect.svelte"


const imageParams = {
    position: {
        x: 0,
        y:0,
        z:0,
    },
    rotation: {
		x: -90,
		y: 0,
		z: 0,
	},
    size: {
        height: 1.5,
        width: 2
    },
    src: "./static/images/chicken_salad.png"
}

const titleBlock = {
	position:{
		x: imageParams.position.x - imageParams.size.width/2,
		y: imageParams.position.y ,
		z: imageParams.position.z - imageParams.size.height/2,
	},
	rotation: {
		x: -90,
		y: 0,
		z: 0,
	},
	size:{
		height:imageParams.size.height/3,
		width: imageParams.size.width	
	},
	borderRadius: {
		topLeft: 0.05,
		topRight:0.05,
		bottomLeft:0,
		bottomRight:0
	},
	color: "#7AE3D0"
}

const descriptionBlock = {
	size:{
		height:imageParams.size.height,
		width: imageParams.size.width	
	},
	position:{
		x: imageParams.position.x - imageParams.size.width/2,
		y: imageParams.position.y,
		get z(){
			return imageParams.position.z + imageParams.size.height + descriptionBlock.size.height/2
		},
	},
	rotation: {
		x: -90,
		y: 0,
		z: 0,
	},
	
	borderRadius: {
		topLeft: 0,
		topRight:0,
		bottomLeft:0.05,
		bottomRight:0.05
	},
	color: "#2B2B2B"
}

const titleText = {
	text: "Jerk Chicken Salad",
	padding: 0.1,
	position:{
		x: titleBlock.position.x,
		y: titleBlock.position.y + 0.05,
		z: titleBlock.position.z - titleBlock.size.height/2,
	},
	rotation: {
		x: -90,
		y: 0,
		z: 0,
	},
	size:{
		height: titleBlock.size.height ,
		width: titleBlock.size.width - 0.2
	},
	font: "https://cdn.aframe.io/fonts/Roboto-msdf.json",
	color: "white",
	wrapCount: "20", //controls how big the text is
	xOffset: 0.1,
	zOffset: 0.001,
	height: 1,
	align: 'center',
	baseline: 'center',
	letterSpacing: 2,
	anchor: 'left'

}

const descriptionText = {
	text: "A fragment Thai inspired chicken salad dish that is light to the mouth and refreshing to the tongue.\n\n Contains roasted chicken, pineapple, breadcrumbs, lime, chilli, coriander and black beams.",
	padding: 0.1,
	size:{
		height: descriptionBlock.size.height ,
		width: descriptionBlock.size.width - 0.2 
	},
	position:{
		x: descriptionBlock.position.x,
		y: descriptionBlock.position.y + 0.05,
		z: descriptionBlock.position.z - descriptionBlock.size.height,
	},
	rotation: {
		x: -90,
		y: 0,
		z: 0,
	},
	
	font: "https://cdn.aframe.io/fonts/Roboto-msdf.json",
	color: "white",
	wrapCount: "30", //controls how big the text is
	xOffset: 0.1,
	zOffset: 0.001,
	height: 1,
	align: 'left',
	baseline: 'center',
	letterSpacing: 2,

}


</script>

<RoundedRect params={titleBlock}/>
<a-text value={titleText.text} position={`${titleText.position.x} ${titleText.position.y} ${titleText.position.z}`} rotation={`${titleText.rotation.x} ${titleText.rotation.y} ${titleText.rotation.z}`} width={titleText.size.width} height={titleText.size.height} color={titleText.color} wrap-count={titleText.wrapCount} z-offset={titleText.zOffset} x-offset={titleText.xOffset} letter-spacing={titleText.letterSpacing} font={titleText.font} align={titleText.align} anchor={titleText.anchor} ></a-text>
<a-image position={`${imageParams.position.x} ${imageParams.position.y} ${imageParams.position.z}`} rotation={`${imageParams.rotation.x} ${imageParams.rotation.y} ${imageParams.rotation.z}`} height={imageParams.size.height} width={imageParams.size.width} src={imageParams.src}></a-image>
<RoundedRect params={descriptionBlock} />
<a-text value={descriptionText.text} position={`${descriptionText.position.x} ${descriptionText.position.y} ${descriptionText.position.z}`} rotation={`${descriptionText.rotation.x} ${descriptionText.rotation.y} ${descriptionText.rotation.z}`} width={descriptionText.size.width} height={descriptionText.size.height} color={descriptionText.color} wrap-count={descriptionText.wrapCount} z-offset={descriptionText.zOffset} x-offset={descriptionText.xOffset} letter-spacing={descriptionText.letterSpacing} font={descriptionText.font} ></a-text>